@import "../../scss/variables.scss";

.aboutMe__content {
  padding-top: 100px;
  display: grid;
  grid-template-columns: 2fr 2fr;
  grid-auto-flow: row;
  justify-items: center;
  align-items: center;
  grid-template-areas: "imgMe right ";

  @media (max-width: $sm) {
    display: flex;
    flex-direction: column;
  }

  .sectionTitle {
    margin-top: 4vh;
    will-change: true;
  }

  &_wrapper {
    padding: 0 $indexSize;
    display: flex;
    flex-direction: column;
    width: 100%;
    font-size: calc($indexSize * 1.3);
    gap: $indexSize;

    button {
      padding: calc($indexSize * 0.5);
      width: 50%;
      margin: 0 auto;
    }
  }

  .imgMe {
    width: 50%;
    box-shadow: 5px 3px 6px 4px #373737;
    border-radius: 10px;

    @media (max-width: 1024px) {
      width: 60%;
    }

    @media (max-width: 768px) {
      width: 85%;
    }

    @media (max-width: 620px) {
      margin: 50px 0;
    }
  }

  .sectionBtn {
    margin-top: 20px;
    background-color: #000c24;
    padding: 10px;
    color: #1d4dcb;
    border-radius: 8px;
    font-size: 18px;
    margin: 20px 0;

    &:hover {
      cursor: pointer;
      color: rgb(255, 251, 0);
    }
  }
}
